<template>
  <div id="app">
    <div class="container">
      <div class="img-random-container">
        <canvas width="60" height="60" ref="randomCanvas" />
      </div>
    </div>
    <div class="subContainer">
      <button type="button" class="btn btn-info" @click="drawImage">랜덤</button>
      <button type="button" class="btn btn-dark ml20" @click="saveImage">저장</button>
    </div>
  </div>
</template>

<script>
export default {
  name: 'App',
  metaInfo: {
    title: 'Random Maker',
    htmlAttrs: {
      lang: 'ko'
    },
  },
  data() {
    return {
      avatarBg: 0,
      avatarFace: 0,
      avatarEye: 0
    }
  },
  methods: {
    async drawImage() {
      // #Todo get file length
      const bgNum = this.getRandomIntInclusive(1, 4)
      const faceNum = this.getRandomIntInclusive(1, 4)
      const eyeNum = this.getRandomIntInclusive(1, 3)

      this.avatarBg = bgNum
      this.avatarFace = faceNum
      this.avatarEye = eyeNum

      const ctx = this.$refs.randomCanvas.getContext("2d");

      const bgObj = new Image();
      const faceObj = new Image();
      const eyeObj = new Image();
      
      const bgSrc = require(`./assets/data/bg/${bgNum}.png`);
      const faceSrc = require(`./assets/data/face/${faceNum}.png`);
      const eyeSrc = require(`./assets/data/eye/${eyeNum}.png`);
      
      bgObj.src = bgSrc
      faceObj.src = faceSrc
      eyeObj.src = eyeSrc

      await bgObj.decode();
      ctx.drawImage(bgObj, 0, 0, 60, 60);

      await faceObj.decode();
      ctx.drawImage(faceObj, 0, 0, 60, 60);

      await eyeObj.decode();
      ctx.drawImage(eyeObj, 0, 0, 60, 60);
    },
    getRandomIntInclusive(min, max) {
      min = Math.ceil(min);
      max = Math.floor(max);
      return Math.floor(Math.random() * (max - min + 1) + min); //The maximum is inclusive and the minimum is inclusive
    },
    saveImage() {
      let downloadLink = document.createElement('a');
      downloadLink.setAttribute('download', `${this.avatarBg}_${this.avatarFace}_${this.avatarEye}.png`);
      let dataURL = this.$refs.randomCanvas.toDataURL('image/png');
      let url = dataURL.replace(/^data:image\/png/,'data:application/octet-stream');
      downloadLink.setAttribute('href', url);
      downloadLink.click();
    }
  },
  mounted() {
    this.drawImage();
  },
}
</script>
